@keyframes fade-in-left {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.projects {
  position: relative;
  font-weight: 600;
  display: inline-block;
  width: 206px;
  flex-shrink: 0;
}
.projects-wrapper {
  position: absolute;
  top: 71px;
  left: calc(50% - 518.5px);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-31xl);
}
.project-1-child {
  position: absolute;
  top: 0;
  left: calc(50% - 515px);
  border-radius: var(--br-mini);
  background-color: var(--color-gray-500);
  width: 1030px;
  height: 192px;
}
.a-one-stop {
  position: absolute;
  top: 81px;
  left: 39px;
  font-size: var(--font-size-xl);
  display: inline-block;
  width: 631px;
  height: 72px;
}
.rectangle-icon,
.rectangle-icon1 {
  position: absolute;
  top: 0;
  left: 95.1px;
  width: 191.5px;
  height: 276.8px;
  object-fit: cover;
}
.rectangle-icon1 {
  top: 6.9px;
  left: 0;
  width: 208.2px;
  height: 249.6px;
}
.gr-mockup {
  position: absolute;
  top: -45px;
  left: 692px;
  width: 286.7px;
  height: 276.8px;
}
.project-1,
.project-2-child {
  position: absolute;
  border-radius: var(--br-mini);
  width: 1030px;
  height: 192px;
}
.project-1 {
  top: 201px;
  left: 22px;
}
.project-1:hover,
.project-2:hover {
  background-color: var(--color-gray-400);
}
.project-2-child {
  top: 0;
  left: calc(50% - 515px);
  background-color: var(--color-gray-500);
}
.dised-website {
  position: absolute;
  top: 27px;
  left: 39px;
  font-weight: 600;
}
.a-central-hub {
  position: absolute;
  top: 81px;
  left: 39px;
  font-size: var(--font-size-xl);
  line-height: 30px;
  display: inline-block;
  width: 631px;
}
.rectangle-icon2 {
  position: absolute;
  top: -140px;
  left: 589px;
  width: 474px;
  height: 456px;
  object-fit: cover;
}
.project-2 {
  position: absolute;
  top: 448px;
  left: 22px;
  border-radius: var(--br-mini);
  width: 1030px;
  height: 192px;
  cursor: pointer;
}
.frame-group {
  position: absolute;
  top: 2174px;
  left: calc(50% - 542.5px);
  width: 1085px;
  height: 708px;
  overflow: hidden;
  opacity: 0;
  text-align: left;
  font-size: var(--font-size-11xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
.frame-group.animate {
  animation: 1s ease 0s 1 normal forwards fade-in-left;
}
