.skills-1-child {
  position: relative;
  border-radius: var(--br-mini);
  background-color: var(--color-gray-500);
  width: 485px;
  height: 104px;
  z-index: 0;
}
.div,
.figma {
  position: absolute;
  margin: 0 !important;
  top: 18px;
  left: calc(50% - 222.5px);
  z-index: 1;
}
.div {
  left: calc(50% + 189.5px);
  text-align: right;
  z-index: 2;
}
.skills-1-inner,
.skills-1-item {
  position: absolute;
  margin: 0 !important;
  top: 55px;
  left: 20px;
  border-radius: var(--br-xl);
  background-color: var(--color-gainsboro-100);
  width: 445px;
  height: 10px;
  z-index: 3;
}
.skills-1-inner {
  background-color: var(--color-coral);
  width: 367px;
  z-index: 4;
}
.skills-1 {
  position: absolute;
  top: 122px;
  left: 24px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-3xs);
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-whitesmoke);
  font-family: var(--font-poppins);
}
