@keyframes fade-in-left {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.skills1 {
  position: relative;
  font-weight: 600;
  display: inline-block;
  width: 130px;
  flex-shrink: 0;
}
.skills-wrapper {
  position: absolute;
  top: 0;
  left: calc(50% - 515.5px);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.frame-child {
  position: absolute;
  top: 740.1px;
  left: 541px;
  width: 187.5px;
  height: 380.9px;
}
.frame-inner,
.frame-item {
  position: absolute;
  top: 741.4px;
  left: 540px;
  width: 98.8px;
  height: 379.6px;
}
.frame-inner {
  top: 740px;
  left: 541px;
  width: 3px;
  height: 381px;
}
.frame-child1,
.vector-icon {
  position: absolute;
  top: 741.4px;
  left: 448.2px;
  width: 93.8px;
  height: 379.6px;
}
.frame-child1 {
  top: 738.1px;
  left: 354.5px;
  width: 187.5px;
  height: 382.9px;
}
.frame-parent {
  position: absolute;
  top: 910px;
  left: calc(50% - 542.5px);
  width: 1085px;
  height: 1229px;
  overflow: hidden;
  opacity: 0;
  text-align: left;
  font-size: var(--font-size-31xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
.frame-parent.animate {
  animation: 1s ease 0s 1 normal forwards fade-in-left;
}
