@keyframes fade-in-left {
  0% {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.contact1 {
  position: relative;
  font-weight: 600;
}
.contact-wrapper,
.frame-child2 {
  position: absolute;
  left: calc(50% - 514.5px);
}
.contact-wrapper {
  top: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.frame-child2 {
  top: 170px;
  border-radius: var(--br-mini);
  background-color: var(--color-gray-500);
  width: 1030px;
  height: 528px;
}
.image-9-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 189.7px;
  height: 200px;
  object-fit: cover;
}
.image-9-wrapper {
  position: absolute;
  top: 244px;
  left: 687px;
  border-radius: var(--br-81xl);
  background-color: var(--color-paleturquoise);
  width: 190px;
  height: 190px;
  overflow: hidden;
}
.linkedin-icon {
  position: relative;
  width: 30px;
  height: 30px;
  object-fit: cover;
  cursor: pointer;
}
.linkedin-parent {
  position: absolute;
  top: 453px;
  left: 687px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-4xs);
}
.frame-container {
  position: absolute;
  top: 2882px;
  left: calc(50% - 540.5px);
  width: 1085px;
  height: 735px;
  overflow: hidden;
  opacity: 0;
  text-align: left;
  font-size: var(--font-size-31xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
.frame-container.animate {
  animation: 1s ease 0s 1 normal forwards fade-in-left;
}
