.rectangle-div {
  position: absolute;
  top: 35px;
  left: 0;
  border-radius: var(--br-mini);
  background-color: var(--color-gray-100);
  width: 408px;
  height: 56px;
}
.name {
  position: absolute;
  top: 0;
  left: 10px;
  font-weight: 600;
}
.type-your-name {
  position: absolute;
  top: 51px;
  left: 18px;
  font-size: var(--font-size-base);
  color: var(--color-gray-600);
}
.rectangle-parent {
  position: absolute;
  top: 208px;
  left: 79px;
  width: 408px;
  height: 91px;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
