@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-31xl: 50px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-gray-100: #1b1b1b;
  --color-gray-200: #141414;
  --color-gray-300: #121212;
  --color-gray-400: rgba(140, 140, 140, 0.15);
  --color-gray-500: rgba(18, 18, 18, 0.5);
  --color-gray-600: rgba(255, 255, 255, 0.2);
  --color-white: #fff;
  --color-coral: #f2672e;
  --color-gainsboro-100: #d9d9d9;
  --color-whitesmoke: #f9fafb;
  --color-paleturquoise: #a9eefd;

  /* Gaps */
  --gap-4xs: 9px;
  --gap-3xs: 10px;

  /* Border radiuses */
  --br-5xl: 24px;
  --br-mini: 15px;
  --br-81xl: 100px;
  --br-xl: 20px;
  --br-9xs: 4px;
}
